import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NavController, ToastController } from '@ionic/angular';
import { AuthConstants } from 'src/app/configuration/auth-constants';
import { IBatch } from 'src/app/models/IBatch';
import { IPO } from 'src/app/models/IPO';
import { IProduct } from 'src/app/models/IProduct';
import { IUser } from 'src/app/models/IUser';
import { HttpService } from 'src/app/services/http.service';
import { StorageService } from 'src/app/services/storage.service';

@Component({
  selector: 'app-product-order-detail',
  templateUrl: './product-order-detail.page.html',
  styleUrls: ['./product-order-detail.page.scss'],
})
export class ProductOrderDetailPage implements OnInit {
  id: string;
  po: IPO;
  new: boolean = false;
  user: IUser;
  edit: boolean;
  batch_init: IBatch = {
    id: 0,
    name: "",
    expirationDate: '2022/01/01',
    productionDate: '2022/01/01',
    expectedSize: 0,
    finishedBatchDate: null,
    state: 0,
    okTotal: 0,
    nokTotal: 0,
    nokHigh: 0,
    nokLow: 0,
    nokOther: 0,
    productId: 0,
    deletedAt: null,
    createdAt:null,
    updatedAt:null,
  };
  product_init: IProduct = {
    id: 0,
    name: "",
    presentation: "",
    GTIN: "",
    internalCode: "",
    weightTarget: 0,
    weightMax: 0,
    weightMin: 0,
    speed: 0,
    state: 0,
    width: 0,
    batches: [this.batch_init],
    deletedAt: null,
    createdAt: null,
    updatedAt: null,

  };
  po_init: IPO = {
    name: "",
    code: "",
    userId: 0,
    description: "",
    state: 0,
    products: [this.product_init],
    batches: [this.batch_init],
    deletedAt: null,
    createdAt: null,
    updatedAt: null,
  };

  constructor(
    private router: Router,
    public toastController: ToastController,
    private activatedRoute: ActivatedRoute,
    private httpClient: HttpService,
    private storageService: StorageService,
    private navCtrl: NavController 
  ) { }

  ngOnInit() {
    this.id = this.activatedRoute.snapshot.paramMap.get('id');
    if(this.id == null){
      this.new = true;
    }
    console.log("new Po:", this.new);
    // this.storageService.get(AuthConstants.USER).then(res=>{
    //   this.user = res.user;
    // });
    if(this.new){
      this.po = this.po_init;
      this.edit = true;
    } else {
      this.httpClient.getPO(this.id).subscribe(po => {
        console.log(po);
        this.po = po;
        if(this.po.state === 2){
          this.edit = false;
        } else {
          this.edit = true;
        }
      })
    }
  }

  goBack(): void {
    this.router.navigate(['home']);
  }

  getBackButtonText() {
    const win = window as any;
    const mode = win && win.Ionic && win.Ionic.mode;
    return mode === 'ios' ? 'Atrás' : 'Atrás';
  }


}
